<template>
  <div class="hello">

   <h1 style="color: rgba(166, 131, 18); float: left;">{{ title }}</h1>
   <img src="../.././style/dota2Icon.png" width="70px" height="70px" style="paddingLeft: 10px"/>
  
    <!-- Element UI -->   
    <el-input
    :style="searchStyle"
    v-model="search"
    placeholder="Search your hero"
    prefix-icon="el-icon-search"/> 

   <el-table
    v-loading="this.$store.state.loading"
    element-loading-text="Loading your heroes..."
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
    :data="filteredheroes.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
    :style="tableStyle">
  <el-table-column prop="img" label="HERO">
        <template slot-scope="props"> 
          <img :src=" `https://api.opendota.com`+props.row.img" width="70px" height="70px" style="borderRadius: 50%"/>
        </template>
  </el-table-column>
  <el-table-column prop="localized_name" label="NAME" >
  </el-table-column>
  <el-table-column prop="pro_win" label="PRO WIN RATE">
  </el-table-column>
  <el-table-column prop="pro_ban" label="PRO BAN COUNT">
  </el-table-column>
  <el-table-column prop="pro_pick" label="PRO PICK COUNT">
  </el-table-column>
  </el-table>

    </div>  
</template>

<!-- VUE -->
<script>
import '../../style/style.css'
import Vue from 'vue'
import '../../style/theme/index.css'
import {mapState} from 'vuex'


export default {
  name: 'Dota2HeroesWinRate',
  data()
  {
    return{
      search: '',
      tableStyle: {
        boxShadow: "0px 0px 20px 20px rgba(30, 60, 92,12)",
        margin: "0 auto",
        width: "90%",
        color: "rgba(166, 131, 18)",
        borderSpacing: "10px",
        },
      searchStyle: {
        marginBottom: "40px",
        }
    }
  },

//Dispatch the loadData (API call)
mounted()
    {
      this.$store.dispatch('loadData')
    },

computed: {
   //vuex mapState
    ...mapState([
     'title'
   ]),

   //Sort heroes based on highest win rate
   sortheroes: function()
   {
     
     let issorted = false;
     let temp = 0;
     while(!issorted)
     {
       issorted = true;
       for(let i=0; i<this.$store.state.herostats.length - 1; i++)
       {
             if(this.$store.state.herostats[i+1].pro_win > this.$store.state.herostats[i].pro_win)
             {
              temp = this.$store.state.herostats[i];
              Vue.set(this.$store.state.herostats, i, this.$store.state.herostats[i + 1]);
              Vue.set(this.$store.state.herostats, i + 1, temp);
              issorted = false
             }
       }
     }
      return this.$store.state.herostats
   },

  //Filter top 10 heroes
  filteredheroes: function () {
     return this.sortheroes.slice(0,10)
     },
},
}
</script>







